:root {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
}

.stars {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0 16px;
  display: flex;
}

.stars__stars {
  justify-content: space-around;
  align-items: center;
  width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__text {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.stars__card-text {
  margin: 0;
  line-height: 14px;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  display: flex;
}

.card__img {
  box-sizing: border-box;
  align-self: center;
  width: 100%;
  height: 134px;
  margin: 32px 0 40px;
  padding: 0 19px;
}

.card__title {
  color: #060b35;
  box-sizing: border-box;
  text-align: justify;
  width: 100%;
  margin: 0;
  padding: 0 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text {
  margin: 0;
}

.card__text--one {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__text--two {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__product {
  color: #616070;
  text-align: justify;
  box-sizing: border-box;
  align-self: flex-start;
  width: 100%;
  margin: 4px 0 16px;
  padding-left: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 24px 0 16px;
  padding: 0 16px;
  display: flex;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

